<template>
   <body>
    <IntroModulo
      :info="module"
      :next="next" 
    />
     <section class="videoArea">
       <div class="videoScope">
          <div id="vimeo" class="video" style="padding:56.25% 0 0 0;position:relative;">
            <iframe :src="selectUrl"
              style="position:absolute;top:0;left:0;width:100%;height:100%;" frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen
              @click.native="finished">
            </iframe>
        </div>
       </div>
       <div class="clasesScope">
        <TablaContenidos 
          :module="module._id"
          :videos="module.videos" />
       </div>
     </section>
     <section class="tools">
       <ExtraModulo :info="module.materials"/>
       <div class="side">
         <Redes type="extra"/>
        <BookHome />
       </div>
     </section>
     <!-- <code><pre>{{$data.next}}</pre></code> -->
   </body>
</template>

<script>

import IntroModulo from '@/components/IntroModulo.vue'
import TablaContenidos from '@/components/TablaContenidos.vue'
import ExtraModulo from '@/components/ExtraModulo.vue'
import BookHome from '@/components/BookHome'
import Redes from '@/components/Redes'

import Bus from '@/helpers/Bus'
 
export default {
  name: 'Modulo',
  components:{
    IntroModulo,
    TablaContenidos,
    ExtraModulo,
    BookHome,
    Redes
  },
  computed: {
  },
  data () {
    return {
      module: {},
      info: {
        id: '1',
        title: 'Comer despierto',
        module: 'Módulo / Semana 1 ',
        description: 'Es un programa de alimentación consciente que enseña hábitos para aprender a comer conectando con nuestras sensaciones y emociones, manteniendo de ese modo una relación sana con la comida.',
      },
      videos: [
        {
          id: '1',
          name: 'primero',
          url: '94964013'
        },
        {
          id: '2',
          name: 'segundo',
          url: '92921329'
        }
      ],
      extra: [
        [
          {
            text: 'Antes de empezar',
            url: '#'
          },
          {
            text: 'Declaración inicial',
            url: '#'
          },
          {
            text: 'Compromiso con uno mismo',
            url: '#'
          },
          {
            text: '¿Qué es comer despierto?',
            url: '#'
          }
        ],
        [
          {
            text: 'Tarea',
            url: '#'
          },
          {
            text: 'Registro',
            url: '#'
          },
        ],
      ],
      next: {},
      selectUrl: 'https://www.youtube.com/embed/jAPdA6eSDls' // welcome
    }
  },
  methods:{
    getModule() {
      let id = this.$router.history.current.params.id
      if (id) {
        this.$http.get('module/'+id,
        { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
        .then(res => {
          this.module = res.data.module
          this.changeVideo(res.data.module.videos[0].url) 
          Bus.$emit('next',res.data.next)
          Bus.$emit('nextData', res.data.next);
        })
        .catch(err => {
          console.log(err);
        })
        }
    },
    changeVideo(url) {
      this.selectUrl = url
    },
    finished(){
      Bus.$emit('finished')
    },
  },
  mounted() {
    Bus.$on('goVide', this.changeVideo)
    Bus.$on('loadModule', this.getModule)
  }
}
</script>

